<template>
	<div class="main">
		<div class="btn">
			<el-button type="primary" icon="el-icon-delete" @click="delSelection()" :disabled="disabledDel">全部删除</el-button>
			<el-button type="primary" icon="el-icon-edit" @click="dialogFormVisible = true">添加</el-button>
		</div>
		<el-table :data="tableDate" ref="multipleTable" @selection-change="handleSelectionChange" class="aip-table">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="学校ID" align="center"></el-table-column>
			<el-table-column prop="name" label="学校名称" align="center"></el-table-column>
			<el-table-column prop="address" label="学校地址" align="center"></el-table-column>
			<el-table-column prop="created_at" label="创建时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalPage">
			</el-pagination>
		</div>


		<el-dialog :visible.sync="dialogFormVisible">
			<el-form :model="form" :rules="rules" ref="form" label-width="100px">
				<el-form-item label="学校名称">
					<el-input v-model="form.name"></el-input>
				</el-form-item>
				<el-form-item label="学校地址">
					<el-input type="textarea" v-model="form.address"></el-input>
				</el-form-item>
				<el-form-item label="人脸库(进)">
					<el-input v-model="form.in_group"></el-input>
				</el-form-item>
				<el-form-item label="人脸库(出)">
					<el-input v-model="form.out_group"></el-input>
				</el-form-item>
				<el-form-item label="mode">
					<el-input v-model="form.mode"></el-input>
				</el-form-item>
				<el-form-item label="用户名">
					<el-input v-model="form.user.username"></el-input>
				</el-form-item>
				<el-form-item label="密码">
					<el-input v-model="form.user.password"></el-input>
				</el-form-item>
				<div v-for="(item,index) in form.grades" :key="index">
					<el-form-item label="年级选择">
						<el-select v-model="item.title" placeholder="请选择">
							<el-option v-for="selectItem in grade" :key="selectItem.value" :label="selectItem.label" :value="selectItem.value">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="班级">
						<el-transfer v-model="item.classes" :data="classList" :titles="['班级', '选中班级']" :button-texts="['取消', '确定']"></el-transfer>
					</el-form-item>
				</div>
				<el-form-item label="操作">
					<el-button type="success" @click="addGrade">添加年级</el-button>
					<el-button type="success" @click="delGrade">删除年级</el-button>
				</el-form-item>

				<div class="submit">
					<el-form-item>
						<el-button type="primary" @click="newSchool">提交</el-button>
					</el-form-item>
				</div>
			</el-form>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/modules/school.js'
	let class_title = [];
	export default {
		data() {
			return {
				dialogFormVisible: false,
				form: {
					// logo: 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg',
					name: '',
					address: '',
					in_group: '',
					out_group: '',
					mode: 1,
					user: {
						'username': '',
						'password': ''
					},
					grades: []
				},
				classList: [],
				classData: [],
				grade: [{
					value: '一年级',
					label: '一年级'
				}, {
					value: '二年级',
					label: '二年级'
				}, {
					value: '三年级',
					label: '三年级'
				}, {
					value: '四年级',
					label: '四年级'
				}, {
					value: '五年级',
					label: '五年级'
				}, {
					value: '六年级',
					label: '六年级'
				},
				 {
				 	value: '初一',
				 	label: '初一'
				 },
				 {
				 	value: '初二',
				 	label: '初二'
				 },
				 {
				 	value: '初三',
				 	label: '初三'
				 }],
				rules: {
					logo: [{
						required: true
					}],
					name: [{
						required: true,
						message: '请填写校名',
						trigger: 'blur'
					}]
				},
				tableDate: [],
				multipleSelection: [],
				disabledDel: true,
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.generateData();
			this.getSchool();
		},
		methods: {

			handleDelete(index, row) {
				console.log(index, row);

			},
			// 获取AIP
			getSchool() {
				var self = this;
				API.schools(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			// 选择表格行
			handleSelectionChange(val) {
				this.multipleSelection = val;
				this.disabledDel = false;

				if (this.multipleSelection == '') {
					this.disabledDel = true;
				}
			},
			// 删除多行
			delSelection(rows) {
				if (rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.delSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},

			// 添加年级
			addGrade() {
				var self = this;
				self.form.grades.push({})
			},
			generateData(_) {
				var self = this;
				var classData = ['一班', '二班', '三班', '四班', '五班', '六班', '七班', '八班', '九班', '十班']
				for (let i = 0; i < classData.length; i++) {
					self.classList.push({
						key: classData[i],
						label: classData[i]
					});
				}
				return self.classList
			},
			delGrade() {
				var self = this;
				self.form.grades.pop({})
			},
			// 添加新的学校		
			newSchool() {
				var self = this;
				self.form.grades.forEach(item => {
					for (var i = 0; i < item.classes.length; i++) {
						item.classes[i] = {
							title: item.classes[i]
						}
					}
				})
				API.school(this.form).then(res => {
					self.dialogFormVisible = false;
					self.getSchool();
					self.form = {};
					self.$message.success('提交成功')
				})
			},
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getSchool();
			},
		}
	}
</script>

<style lang="scss" scoped>
	.btn {
		margin-bottom: 1vw;
	}

	.el-table {
		border: 1px solid #dfe6ec;
	}

	.block {
		text-align: center;
		margin-top: 1vw;
	}

	.submit {
		margin-top: 1vw;
		text-align: right;
	}
</style>
